import { View, Text, TouchableWithoutFeedback, Animated } from "react-native";
import React from "react";
import {
    ButtonOuter,
    ButtonInner,
    ButtonFaceGradient,
} from "./styles";
import { useCallback, useState, useMemo } from "react";
import { useRef } from "react";

type ButtonProps = {
    onPress: () => void;
    size: number;
    distance: number;
    blur: number;
    borderRadius: number;
}

