// --positionXOpposite: -20px;
// --positionY: 20px;
// --positionYOpposite: -20px;
// --angle: 145deg;
// --blur: 60px;
// --textColor: #001f3f;
// --textColorOpposite: #e0e0e0;
// --baseColor: #e0e0e0;
// --darkColor: #bebebe;
// --lightColor: #ffffff;
// --firstGradientColor: #e0e0e0;
// --secondGradientColor: #e0e0e0;
// --size: 300px;
// --radius: 50px

function NeumorphismSimpleButton(props: ButtonProps) {
    const { onPress, size = 150, distance = 20, blur = 35, borderRadius = 30 } = props;
    const blurAnimation = useRef(new Animated.Value(0)).current;

    const [isDown, setDown] = useState(false);




    const blurIn = () => {
        // Will change fadeAnim value to 1 in 5 seconds
        Animated.timing(blurAnimation, {
            toValue: 1,
            duration: 5000,
            useNativeDriver: false
        }).start();
    };

    const blurOut = () => {
        // Will change fadeAnim value to 0 in 3 seconds
        Animated.timing(blurAnimation, {
            toValue: 0,
            duration: 3000,
            useNativeDriver: false
        }).start();
    };

    const handlePressIn = useCallback(() => {
        blurIn()
        setDown(true);
    }, [setDown]);

    const handlePressOut = useCallback(() => {
        setDown(false);
    }, [setDown]);

    const gradientColors = useMemo(() => isDown ? ['#d1d9e6', '#ffffff'] : ['#ffffff', '#d1d9e6'], [isDown]);
    const gradientStart = useMemo(() => isDown ? ({ x: 145, y: 125 }) : ({ x: 145, y: 125 }), [isDown]);
    const gradientEnd = useMemo(() => isDown ? ({ x: 145, y: 145 }) : ({ x: 1, y: 145 }), [isDown]);

    const shadowColors = useMemo(() => isDown ? ['#bebebe', '#ffffff'] : ['#fff', '#bebebe'], [isDown]);

    console.log(blurAnimation)
    return (
        <TouchableWithoutFeedback
            onPress={onPress}
            onPressIn={handlePressIn}
            onPressOut={handlePressOut}>
            {/* <View style={{ width: size + distance, height: size + distance, borderWidth: 1, justifyContent: 'center', alignItems: 'center' }}> */}

            <ButtonOuter shadowColor={shadowColors[0]} borderRadius={borderRadius} blur={blur} distance={distance} size={size}>
                <ButtonInner shadowColor={shadowColors[1]} borderRadius={borderRadius} blur={blur} distance={distance} size={size}>
                    <ButtonFaceGradient angle={145}
                        angleCenter={{ x: 0.5, y: 0.5 }} useAngle={true} borderRadius={borderRadius} distance={distance} size={size} colors={gradientColors}  >
                    </ButtonFaceGradient>
                </ButtonInner>
            </ButtonOuter>
            {/* </View> */}

        </TouchableWithoutFeedback>
    );
}

NeumorphismSimpleButton.defaultProps = {

}

export default NeumorphismSimpleButton;
